___CSS_LOADER_EXPORT___.push([module.id, `body {
  margin: 0;
  font-family: "Nunito";
}

.hero-container {
  padding: 0 0 0 2.5rem;
}

.hero-container > h1 {
  margin-bottom: 0;
  font-size: 1.5rem;
  font-weight: 800;
  color: rgb(83, 117, 228);
}

.hero-container > p {
  margin-top: 0.2rem;
  font-size: 1rem;
  font-weight: 300;
}

.deadline-container {
  border-top: 2px solid rgb(233, 233, 233);
}

.top-bar {
  display: grid;
  grid-template-columns: 1fr 30px;
  align-items: center;
  margin: 0;
  padding: 0;
}

.top-bar > .reminder-title {
  padding: 0;
  margin: 0;
}

.bottom-overdue,
.bottom-daily,
.bottom-weekly,
.bottom-monthly,
.bottom-yearly,
.bottom-misc {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  padding: 0 0 1.5rem 1.5rem;
}

.top > p {
  font-size: 0.7rem;
  font-weight: 600;
  color: rgb(195, 195, 195);
  margin: 0 0 1rem 0.2rem;
  padding: 1rem 0 0 2.5rem;
}

.card-container {
  border-radius: 1rem;
  width: 10rem;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.1),
    -4px -4px 10px rgba(255, 255, 255, 0.7);
  padding: 1rem;
  margin: 0 0 0 1rem;
}

.card > .reminder-title {
  font-size: 1rem;
  font-weight: 600;
  margin: 0;
}

.card > .reminder-description {
  font-size: 0.8rem;
  margin: 0.2rem 0 0 0;
  color: rgb(170, 170, 170);
}

.hidden {
  display: none;
}

.date-container > .date {
  font-size: 0.7rem;
  margin: 1rem 0 0 0;
  color: rgb(170, 170, 170);
}

.bottom-bar {
  margin-top: 1rem;
  display: grid;
  grid-template-columns: 2rem 1fr 2rem 2rem;
}

.material-symbols-outlined {
  font-variation-settings: "FILL" 0, "wght" 400, "GRAD" 0, "opsz" 24;
  transform: scale(0.8);
}

.flag {
  color: rgb(255, 0, 0);
  font-variation-settings: "FILL" 1;
}

form {
  padding: 1.5rem 1rem;
  border-radius: 1rem;
  margin: 0 0 2.5rem 2.5rem;
  width: fit-content;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.1),
    -4px -4px 10px rgba(255, 255, 255, 0.7);
}

legend {
  font-weight: 600;
}

form button {
  border: 1px solid rgb(83, 117, 228);
  padding: 0.5rem 1rem;
  color: white;
  background-color: rgb(83, 117, 228);
  font-weight: 800;
  border-radius: 0.2rem;
}

.form-top-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1.5rem;
}

li {
  display: grid;
  grid-template-columns: 8rem 10rem;
  margin: 1rem 0.4rem;
  font-weight: 100;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

input {
  font-family: "Nunito";
}
